<template>
  <div v-if='songInfo'>
    <div :class="[{'cdStyle':cdStyle},{'vinylStyle':!cdStyle}]">
      <div :class="['blurCoverBack',{'loaded':!loaded}]" :style="playerBack"></div>
      <div :class="['cdBox',{'cdSlideIn':loaded}]">
        <div class="vinylBack" v-if='!cdStyle'></div>
        <img :src='songInfo.cover' :class="['cd',{'pause':!playing}]"/>
        <div class="cdLlight"></div>
        <div class="cdShadow"></div>
      </div>
    </div>   
    <div class="songInfo">
      <div>{{songInfo.name}}</div>
      <div><span>{{artists}}</span></div>
    </div>     
  </div>
</template>
<script>
import controller from './controller'
export default {
  name: 'recordPlayer',
  props: ['songInfo', 'cdStyle','playing'],
  components:{
    controller
  },
  computed:{
    artists(){
      if(this.songInfo.artist){
        return this.songInfo.artist.map(v=> v.name).join(',')
      }
    }
  },
  data() {
    return {
      playerBack: {
        backgroundImage: ""
      },
      loaded: false,
      songSrc: ''
    }
  },
  watch: {
    songInfo: function (v, oldVal) {
      this.loaded = false;
      let _img = new Image()
      _img.src = v.cover
      _img.onload = () => {
        setTimeout(() => {
          this.loaded = true
          this.playerBack.backgroundImage = "url('" + v.cover + "')"
        }, 20)
        this.songSrc = "//music.163.com/song/media/outer/url?id=" + v.id + ".mp3"
      }
    }
  }
}

</script>
<style>
.blurCoverBack {
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center;
  position: absolute;
  width: 120%;
  height: 120%;
  top: -10%;
  bottom: 0;
  left: -10%;
  right: 0;
  z-index: 1;
  -webkit-filter: blur(20px);
  -moz-filter: blur(20px);
  -ms-filter: blur(20px);
  filter: blur(20px);
  opacity: .7;
  transition: opacity 2s ease-in;
}
.cdBox {
  transform: translateY(1800px);
  position: absolute;
  top: -300px;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 2;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  align-items: center;
}
.songInfo{  position: absolute;
  bottom: 160px;
  text-align: center;
  left:0;
  right:0;
  font-size: 20px;
  z-index: 999;
  color: #fff;
  text-shadow: -1px -1px 3px rgba(0,0,0,.3);}
.songInfo span{font-size: 80%;display: inline-block;margin:0 20px;color: #ddd}
.vinylBack, .vinylStyle .cdShadow {
  background-size: 800px auto;
  width: 800px;
  height: 800px;
  position: absolute;
}
